import React from 'react';
import { useState } from 'react';
import Button from '@material-ui/core/Button';
import AddIcon from '@material-ui/icons/Add';
import '../css/Blog.css';

function Blog(props) {
    const [blog,setBlog] = useState({
        title:"",
        content:"",
    });
   const [val,setVal] =  useState("");
    const InputEvent=(event)=>{
        const {value,name} = event.target;
        setBlog((preVal)=>{
            // console.log(preVal);
            return{
                ...preVal,
                [name]:value,
            }
        })
    }
    const addBlog=()=>{
       
        if(blog.title===""&&blog.content===""){
            setVal("please type something");
        }else{
            props.data(blog);
            setBlog({
                title:"",
                content:"",
            })
            setVal("Blog Add check All blog section")
           }
        }
    return (<>
            
                    <h2 className='blog_h2'>{val}</h2>
                <div className="add_blogs">
                <form onSubmit={(event)=>event.preventDefault()}>
                    <input 
                    type="text" 
                    placeholder="Title" 
                    value={blog.title}  
                    name="title"
                    onChange={InputEvent} 
                    autoComplete="off"/>
                    <textarea
                    rows="" 
                    column="" 
                    value={blog.content}
                    name="content"
                    onChange={InputEvent}
                    placeholder="Write a Blog..">
                    </textarea>
                    <Button onClick={addBlog}><AddIcon className="circle_btn"/></Button>
                </form>
                </div>
            
                </>)
}

export default Blog;
