import React from 'react'
import { useEffect } from 'react';
import { useState } from 'react';
import { Link } from 'react-router-dom';
import '../css/AllBlog.css';


function Allblog(props) {
  function getData(){
    let list = localStorage.getItem('values');
      console.log(list);
      if(list){
          return JSON.parse(localStorage.getItem('values'));
      }else{
          return [];
      }
  }
 
  const[data,setData] = useState(getData());
  const [arr,setArr] = useState(props.main);
  // console.log(arr);
 useEffect(()=>{
   localStorage.setItem('values',JSON.stringify(data))

 },[])
  
  useEffect(()=>{
    localStorage.setItem('values', JSON.stringify(arr));
    // localStorage.clear('values')
  }, [arr]);
  return(<>
    <h1>Add blogs from home section</h1>
  {
    arr.map((val,ind)=>{
      return(<>
      <div className="all_blog" key={ind}>
        <h2>{val.title}<Link to={`/blog/${ind}`}>View..</Link></h2>
    </div>
    
      </>)
    })
  }
 
    
  </>
  )
}
export default Allblog;
